import React, { useState, useEffect, useRef } from 'react';
import confetti from 'canvas-confetti';
import { GameProvider, useGame } from '../../context/GameContext';
import { useUser } from '../../context/UserContext';
import { useTextToSpeech } from '../../hooks/useTextToSpeech';
import GameShell from '../GameUI/GameShell';
import Button from '../Button/Button';
import * as styles from './TypeTheWord.module.css';

// --- Interfaces ---
interface AlternatePronunciation {
  pronunciation: string;
  note?: string;
}

export interface SpellingWord {
  word: string;
  pronunciation?: string;
  definition?: string;
  sentence?: string;
  languageOfOrigin?: string;
  etymologyDetails?: string;
  partOfSpeech?: string[];
  alternateDefinitions?: string[];
  alternatePronunciations?: AlternatePronunciation[];
  rootWord?: string;
  rootMeaning?: string;
  silentLetters?: string[];
  spellingPattern?: string;
  difficulty?: string;
  audioUrl?: string; // Legacy support
}

interface TypeTheWordProps {
  words: SpellingWord[];
}

const MAX_QUESTIONS = 3;

interface QuestionConfig {
  id: string;
  label: string;
  icon: string;
  getValue: (w: SpellingWord) => string | undefined | null;
  getAudioValue?: (w: SpellingWord) => string | undefined | null;
}

const WordDetailCard: React.FC<{
  word: SpellingWord;
  onNext: () => void;
}> = ({ word, onNext }) => {
  return (
    <div className={styles.detailCard}>
      <h2 className={styles.detailWord}>{word.word}</h2>
      <div className={styles.detailPronunciation}>
        {word.pronunciation && `/${word.pronunciation}/`}
      </div>

      <div className={styles.detailGrid}>
        {word.definition && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Definition</span>
            <p>{word.definition}</p>
          </div>
        )}
        {word.sentence && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Example</span>
            <p>{word.sentence}</p>
          </div>
        )}
        {word.languageOfOrigin && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Origin</span>
            <p>{word.languageOfOrigin}</p>
          </div>
        )}
        {word.etymologyDetails && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Etymology</span>
            <p>{word.etymologyDetails}</p>
          </div>
        )}
        {word.partOfSpeech && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Part of Speech</span>
            <p>{word.partOfSpeech.join(`, `)}</p>
          </div>
        )}
        {word.spellingPattern && (
          <div className={styles.detailItem}>
            <span className={styles.detailLabel}>Pattern</span>
            <p>{word.spellingPattern}</p>
          </div>
        )}
      </div>

      <div className={styles.detailActions}>
        <Button variant="primary" onClick={onNext}>
          Next Word ‚Üí
        </Button>
      </div>
    </div>
  );
};

const TypeTheWordContent: React.FC<TypeTheWordProps> = ({ words }) => {
  const { state, dispatch } = useGame();
  const { user, updateProgress, incrementStreak } = useUser();
  const { speak, isSpeaking } = useTextToSpeech();

  const [userInput, setUserInput] = useState(``);
  const inputRef = useRef<HTMLInputElement>(null);

  // --- Hint State ---
  const [revealedQuestions, setRevealedQuestions] = useState<Set<string>>(
    new Set(),
  );
  const [activeQuestionId, setActiveQuestionId] = useState<string | null>(null);

  const currentWord = words[state.currentRound];
  const questionsCount = revealedQuestions.size;

  // Auto-focus & Reset when round changes
  useEffect(() => {
    if (state.status === `playing`) {
      setUserInput(``);
      setRevealedQuestions(new Set());
      setActiveQuestionId(null);
      inputRef.current?.focus();
    }
  }, [state.currentRound, state.status]);

  const handlePlayAudio = () => {
    if (!currentWord) return;
    speak(currentWord.word);
    inputRef.current?.focus();
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (state.status !== `playing` || !currentWord) return;

    const trimmedInput = userInput.trim();
    const isCorrect =
      trimmedInput.toLowerCase() === currentWord.word.toLowerCase();

    if (isCorrect) {
      confetti({
        particleCount: 80,
        spread: 70,
        origin: { y: 0.6 },
      });

      updateProgress({ wordsMastered: (user?.wordsMastered || 0) + 1 });

      if (state.currentRound === words.length - 1) {
        updateProgress({ labsCompleted: (user?.labsCompleted || 0) + 1 });
        incrementStreak();
      }
    }

    dispatch({
      type: `SUBMIT_ANSWER`,
      isCorrect,
      question: currentWord.word,
      userAnswer: trimmedInput,
      correctAnswer: currentWord.word,
    });

    if (isCorrect) {
      // Auto advance only on success
      setTimeout(() => {
        dispatch({ type: `NEXT_ROUND` });
      }, 1500);
    }
    // On error, we wait for user to click Next in WordDetailCard
  };

  // --- Hint Logic ---
  const availableQuestions: QuestionConfig[] = [
    {
      id: `definition`,
      label: `Definition`,
      icon: `üìñ`,
      getValue: (w: SpellingWord) => w.definition,
    },
    {
      id: `sentence`,
      label: `Sentence`,
      icon: `üó£Ô∏è`,
      getValue: (w: SpellingWord) =>
        w.sentence?.replace(new RegExp(w.word, `gi`), `____`),
      getAudioValue: (w: SpellingWord) => w.sentence,
    },
    {
      id: `origin`,
      label: `Origin`,
      icon: `üåç`,
      getValue: (w: SpellingWord) => w.languageOfOrigin,
    },
    {
      id: `partOfSpeech`,
      label: `Part of Speech`,
      icon: `üè∑Ô∏è`,
      getValue: (w: SpellingWord) => w.partOfSpeech?.join(`, `),
    },
    {
      id: `altPronunciation`,
      label: `Alt Pronunciation`,
      icon: `üì¢`,
      getValue: (w: SpellingWord) =>
        w.alternatePronunciations?.map((ap) => ap.pronunciation).join(`, `),
    },
    {
      id: `altDefinition`,
      label: `Alt Definition`,
      icon: `üìò`,
      getValue: (w: SpellingWord) => w.alternateDefinitions?.join(`; `),
    },
    {
      id: `root`,
      label: `Root Word`,
      icon: `üå±`,
      getValue: (w: SpellingWord) =>
        w.rootWord
          ? `${w.rootWord} ${w.rootMeaning ? `(${w.rootMeaning})` : ``}`
          : null,
    },
  ];

  const handleAskQuestion = (questionId: string) => {
    const info = availableQuestions.find((q) => q.id === questionId);
    if (!info) return;

    const displayValue = info.getValue(currentWord!);
    const audioValue = info.getAudioValue
      ? info.getAudioValue(currentWord!)
      : displayValue;

    // Handle missing value case
    if (!displayValue) {
      speak(
        `This word ${
          currentWord.word
        } does not have any ${info.label.toLowerCase()}.`,
      );
    } else if (audioValue) {
      speak(audioValue);
    }

    if (revealedQuestions.has(questionId)) {
      setActiveQuestionId(questionId);
      return;
    }

    if (questionsCount >= MAX_QUESTIONS) return;

    setRevealedQuestions((prev) => new Set(prev).add(questionId));
    setActiveQuestionId(questionId);
  };

  if (!currentWord && state.status === `playing`) return <div>Loading...</div>;

  return (
    <GameShell
      title="Spelling Challenge"
      description={
        <div style={{ maxWidth: `600px`, margin: `0 auto` }}>
          <h3
            style={{
              fontSize: `1.2rem`,
              fontWeight: `600`,
              marginBottom: `0.5rem`,
              color: `#4a5568`,
            }}
          >
            How to Play:
          </h3>
          <div style={{ textAlign: `left` }}>
            <ul
              style={{
                listStyle: `none`,
                padding: 0,
                margin: `0 auto`,
                maxWidth: `500px`,
                display: `flex`,
                flexDirection: `column`,
                gap: `0.75rem`,
              }}
            >
              <li
                style={{
                  display: `flex`,
                  alignItems: `flex-start`,
                  gap: `0.75rem`,
                }}
              >
                <span style={{ fontSize: `1.25rem` }}>üîä</span>
                <span>
                  <strong>Listen:</strong> Click &apos;Play Word&apos; to hear
                  the pronunciation.
                </span>
              </li>
              <li
                style={{
                  display: `flex`,
                  alignItems: `flex-start`,
                  gap: `0.75rem`,
                }}
              >
                <span style={{ fontSize: `1.25rem` }}>‚úçÔ∏è</span>
                <span>
                  <strong>Type:</strong> Enter the correct spelling in the box
                  and click &apos;Check.&apos;
                </span>
              </li>
              <li
                style={{
                  display: `flex`,
                  alignItems: `flex-start`,
                  gap: `0.75rem`,
                }}
              >
                <span style={{ fontSize: `1.25rem` }}>üí°</span>
                <span>
                  <strong>Get Help:</strong> Use the buttons below to ask the
                  pronouncer questions like Definition or Origin.
                </span>
              </li>
            </ul>
          </div>
        </div>
      }
      onStart={() =>
        dispatch({ type: `START_GAME`, totalRounds: words.length })
      }
    >
      <div className={styles.playArea}>
        {state.status === `error` ? (
          <WordDetailCard
            word={currentWord!}
            onNext={() => dispatch({ type: `NEXT_ROUND` })}
          />
        ) : (
          <>
            <Button
              variant="secondary"
              size="small"
              onClick={handlePlayAudio}
              disabled={isSpeaking}
            >
              {isSpeaking ? `üîä Speaking...` : `üîä Play Word`}
            </Button>

            <form onSubmit={handleSubmit} className={styles.inputGroup}>
              <input
                ref={inputRef}
                type="text"
                value={userInput}
                onChange={(e) => setUserInput(e.target.value)}
                disabled={state.status !== `playing`}
                className={`
                  ${styles.input} 
                  ${state.status === `success` ? styles.correct : ``}
                  ${state.status === `error` ? styles.shake : ``}
                `}
                placeholder="Type here..."
                autoComplete="off"
                autoCorrect="off"
                autoCapitalize="off"
              />
              <Button
                variant="primary"
                type="submit"
                disabled={!userInput || state.status !== `playing`}
              >
                Check
              </Button>
            </form>

            {/* --- Questions Section --- */}
            {state.status === `playing` && (
              <div className={styles.hintsContainer}>
                <div className={styles.hintsHeader}>
                  <span>Ask the Pronouncer:</span>
                  <span>
                    Questions Asked:{` `}
                    <strong>
                      {questionsCount}/{MAX_QUESTIONS}
                    </strong>
                  </span>
                </div>

                <div className={styles.questionsGrid}>
                  {availableQuestions.map((q) => {
                    const isRevealed = revealedQuestions.has(q.id);
                    const canAsk = isRevealed || questionsCount < MAX_QUESTIONS;

                    return (
                      <button
                        key={q.id}
                        type="button"
                        className={`${styles.questionBtn} ${
                          activeQuestionId === q.id ? styles.activeQuestion : ``
                        }`}
                        onClick={() => handleAskQuestion(q.id)}
                        disabled={!canAsk}
                      >
                        <span>{q.icon}</span>
                        <span>{q.label}</span>
                        {isRevealed && (
                          <span
                            style={{ fontSize: `0.75rem`, color: `#16a34a` }}
                          >
                            ‚úì
                          </span>
                        )}
                      </button>
                    );
                  })}
                </div>

                {activeQuestionId && (
                  <div className={styles.answerDisplay}>
                    <span className={styles.answerLabel}>
                      {
                        availableQuestions.find(
                          (q) => q.id === activeQuestionId,
                        )?.label
                      }
                    </span>
                    {availableQuestions
                      .find((q) => q.id === activeQuestionId)
                      ?.getValue(currentWord!) || <em>Not available</em>}
                  </div>
                )}
              </div>
            )}
          </>
        )}

        <div
          className={`${styles.feedback} ${
            state.status !== `playing` ? styles[state.status] : ``
          }`}
        >
          {state.status === `success` && `Correct! üéâ`}
          {state.status === `error` && `Incorrect. Review the details below.`}
        </div>
      </div>
    </GameShell>
  );
};

const TypeTheWord: React.FC<TypeTheWordProps> = (props) => (
  <GameProvider>
    <TypeTheWordContent {...props} />
  </GameProvider>
);

export default TypeTheWord;
